.card_custon {
  margin-bottom: 1.875rem;
  transition: all 0.5s ease-in-out;
  position: relative;
  border: 0px solid transparent;
  border-radius: 0.4rem;
  box-shadow: 0px 2px 6px 0px rgb(62 73 84 / 20%);
}

.fondo ion-item {
  margin: 0 auto;
  background: #0a6fb8;
  --background: #0a6fb8;
  color: #ffffff;
  font-size: 1rem;
}

/*-ACORDION----------------------------------------*/
.acordion__fondo {
  background: #0a6fb8;
}

.acordion__item__content {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  font-size: 0.9rem;
  border-bottom: solid 1px #dfe1e5;
}
/*-ESTILO-BOTONES---------------------------------*/
.custom-button {
  --border-radius: 0.2rem !important;
  --background: #293f76;
  font-size: 0.9rem;
  text-transform: capitalize;
}

.button__capitalize {
  text-transform: capitalize;
}

.botton__link {
  background: none;
  border: 0;
  color: #444;
  display: inline;
  font-family: arial, sans-serif;
  font-size: 0.8em;
  line-height: 1em;
  margin-right: 8px;
  padding: 0;
  text-decoration: underline;
  text-transform: initial;
}

ion-accordion > ion-item.ion-activatable.ion-focusable > ion-label {
  color: #ffffff;
}

ion-accordion ion-item ion-icon {
  color: #ffffff;
}

ion-item {
  font-size: 0.9rem;
}
ion-item ion-label {
  margin: 4px 0px;
}

ion-button {
  border-radius: 0.2rem !important;
}

ion-card-header {
  padding: 0.6rem;
  padding-bottom: 0.2rem !important;
}

ion-card-content ion-label {
  color: #5a5a5a;
}

.item .sc-ion-label-ios-h {
  font-size: 0.9rem;
}
.title-default {
  font-weight: 500;
}

.searchContainer {
  border-bottom: 1px solid #dfe1e5;
}

.searchbar-input.sc-ion-searchbar-ios {
  border: 1px solid #dfe1e5;
  background: #ffffff;
  color: #5a5a5a;
  font-size: 0.9rem;
}

.button-inner {
  color: #293f76 !important;
}

ion-item.item-interactive {
  --background: #ffffff;
  --color: #333333;
  --padding-top: 0.4rem;
  --padding-bottom: 0.4rem;
  --padding-start: 0.5rem;
  --padding-end: 0.5rem;
  border-radius: 0.2rem;
  border: 1px solid #dfe1e5;
  --height: 30px !important;
}
/*-CUSTOM-CLASS-------------------*/
.d__flex {
  display: flex;
}
.justify__content__between {
  justify-content: space-between;
}

.justify__content__center {
  justify-content: center;
}
