  color: #1d1d1d;
}

#listResults .slider {
  margin: 25px 0;
}
#listResults .slider-box {
  width: 90%;
  margin: 25px auto;
}
#listResults input {
  width: 10%;
}
#listResults label {
  border: none;
  display: inline-block;
  margin-right: -4px;
  vertical-align: top;
  width: 30%;
}

.plus-minus-input .input-icon {
  font-size: 13px;
  color: #aaaaaa;
}
.plus-minus-input .custom-btn {
  border-radius: 0;
  height: 40px;
  padding: 8px 12px;
  background: #ffffff;
  border: 1px solid #c8c8c8;
}
.plus-minus-input .custom-btn:hover,
.plus-minus-input .custom-btn:focus,
.plus-minus-input .custom-btn.active {
  box-shadow: none;
  outline: none;
}
.plus-minus-input .form-control {
  height: 40px;
  border-top: 1px solid #c8c8c8;
  border-bottom: 1px solid #c8c8c8;
  border-left: 0px solid #c8c8c8;
  border-right: 1px solid #c8c8c8;
}
.plus-minus-input .form-control:hover,
.plus-minus-input .form-control:focus,
.plus-minus-input .form-control:active {
  border-top: 1px solid #c8c8c8;
  border-bottom: 1px solid #c8c8c8;
  border-left: 0px solid #c8c8c8;
  border-right: 0px solid #c8c8c8;
}

.new-arrival-product .new-arrivals-img-contnent {
  overflow: hidden;
}
.new-arrival-product .new-arrivals-img-contnent img {
  width: 100%;
  transition: all 0.5s;
}
.new-arrival-product:hover .new-arrivals-img-contnent img {
  transform: scale(1.5) translateY(12%);
  -moz-transform: scale(1.5) translateY(12%);
  -webkit-transform: scale(1.5) translateY(12%);
  -ms-transform: scale(1.5) translateY(12%);
  -o-transform: scale(1.5) translateY(12%);
}

/*
===================================
    list view
===================================*/
.new-arrival-content .item {
  font-size: 12px;
  color: #3e4954;
}
.new-arrival-content h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
}
.new-arrival-content h4 a {
  color: #000;
}
.new-arrival-content .price {
  font-weight: 600;
  color: #36c95f;
  font-size: 24px;
  margin-bottom: 0;
  float: right;
}
.new-arrival-content p {
  font-size: 14px;
  color: #828690;
  margin-bottom: 3px;
  line-height: 24px;
}
.new-arrival-content .text-content {
  margin-top: 10px;
}
@media only screen and (max-width: 575px) {
  .new-arrival-content .price {
    float: left;
    margin-top: 10px;
  }
}
.new-arrival-content.text-center .price {
  float: unset;
}

.success-icon {
  color: #2bc155;
  font-size: 16px;
}

.comment-review {
  margin-bottom: 15px;
  display: table;
  width: 100%;
}
.comment-review .client-review {
  color: #828690;
  padding-right: 20px;
  text-decoration: underline !important;
  font-size: 14px;
}
.comment-review .span {
  color: #828690;
  font-size: 14px;
}

.star-rating li {
  display: inline-block;
}
.star-rating li i {
  color: gold;
}

